<template>
    <div class="box">
        <navbar></navbar>
        <div class="content">
            <div class="center-box warp-1200 ov">
                <centerbar></centerbar>
                <div class="right fl">
                    <p class="balance"><span>提现记录</span></p>
                    <ul  v-infinite-scroll="load"  infinite-scroll-disabled="disabled" style="overflow:auto;height: 600px">
                        <div class="tit"><span>地址</span><span>金额</span><span>时间</span><span>状态</span></div>
                        <li v-for="item in withdrawList"><span>{{item.address}}</span><span>{{item.total}}usdt</span><span>{{item.create_time}}</span><span v-if="item.state == 1" class="wait">待审核</span><span v-if="item.state == 2" class="warning">待入账</span><span v-if="item.state == 3" class="danger">已拒绝</span><span v-if="item.state == 4" class="succ">已完成</span></li>
                        <p style="font-size:14px;width: 100%;text-align: center;margin-top: 10px" v-if="loading ">加载中...</p>
                        <p style="font-size:14px;width: 100%;text-align: center;margin-top: 10px" v-if="noMore && this.withdrawList.length >= 10">没有更多了</p>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Navbar, Centerbar } from '@/components'
    import {withdrawList} from "../../api/center";
    export default {
        name: "Withdraw",
        components: {
            Navbar,
            Centerbar
        },
        computed: {
            disabled () {
                return this.loading || this.noMore
            }
        },
        data(){
            return {
                withdrawList:[],
                noMore:false,
                loading:false,
                page:1
            }
        },
        mounted()
        {
            //this.getwithdrawList()
        },
        methods: {
            load () {
                this.loading = true
                this.getWithdrawList();
            },
            getWithdrawList()
            {
                withdrawList({page:this.page}).then(res => {
                    this.loading = false
                    if (res.length < 10)
                    {
                        this.noMore = true
                    }
                    if (this.page == 1)
                    {
                        this.withdrawList = res
                    } else
                    {
                        this.withdrawList = this.withdrawList.concat(res)
                    }
                    this.page++

                })
            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/withdraw.css";
     .box {height: 100%;background: #F2F4F9;}
</style>



